  left: 0;
  right: 0;
}
.uk-position-top-left {
  top: 0;
  left: 0;
}
.uk-position-top-right {
  top: 0;
  right: 0;
}
.uk-position-bottom-left {
  bottom: 0;
  left: 0;
}
.uk-position-bottom-right {
  bottom: 0;
  right: 0;
}
/*
 * Cover
 */
.uk-position-cover {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
/*
 * Relative
 */
.uk-position-relative {
  position: relative !important;
}
/*
 * Z-index
 */
.uk-position-z-index {
  z-index: 1;
}
/* Display
 ========================================================================== */
/*
 * Display
 */
.uk-display-block {
  display: block !important;
}
.uk-display-inline {
  display: inline !important;
}
.uk-display-inline-block {
  display: inline-block !important;
}
/*
 * Visibility
 * Avoids setting display to `block` so it works also with `inline-block` and `table`
 */
/* Desktop and bigger */
@media (min-width: 960px) {
  .uk-visible-small {
    display: none !important;
  }
  .uk-visible-medium {
    display: none !important;
  }
  .uk-hidden-large {
    display: none !important;
  }
}
/* Tablets portrait */
@media (min-width: 768px) and (max-width: 959px) {
  .uk-visible-small {
    display: none !important;
  }
  .uk-visible-large {
    display: none !important ;
  }
  .uk-hidden-medium {
    display: none !important;
  }
}
/* Phone landscape and smaller*/
@media (max-width: 767px) {
  .uk-visible-medium {
    display: none !important;
  }
  .uk-visible-large {
    display: none !important;
  }
  .uk-hidden-small {
    display: none !important;
  }
}
/* Remove from the flow and screen readers on any device */
.uk-hidden {
  display: none !important;
  visibility: hidden !important;
}
/* It's hidden, but still affects layout */
.uk-invisible {
  visibility: hidden !important;
}
/* Show on hover */
.uk-visible-hover:hover .uk-hidden,
.uk-visible-hover:hover .uk-invisible {
  display: block !important;
  visibility: visible !important;
}
.uk-visible-hover-inline:hover .uk-hidden,
.uk-visible-hover-inline:hover .uk-invisible {
  display: inline-block !important;
  visibility: visible !important;
}
/* Hide on touch */
.uk-touch .uk-hidden-touch,
.uk-notouch .uk-hidden-notouch {
  display: none !important;
}
@utility-container-max-width : 1000px
@utility-container-large-max-width: @utility-container-max-width;
.bt-icon {
  width: 0.85em;
  height: 0.85em;
  position: relative;
  fill: currentColor;
  display: inline-block;
}
a > .bt-icon {
  pointer-events: none;
}
.bt-icon--smaller {
  width: 0.65em;
  height: 0.65em;
}
.bt-icon--standalone {
  width: 1em;
  height: 1em;
}
.bt-icon--larger {
  width: 1.25em;
  height: 1.25em;
  transform: translateY(0.2em);
}
.Wrapper {
  max-width: 2000px;
  margin: 0 auto;
}
.Header {
  height: 160px;
  background: #fff;
  display: flex;
  align-content: center;
  justify-content: center;
  z-index: 10;
  box-shadow: 0 1px 2px #f3f2f0;
}
.Header ul {
  display: flex;
  margin: 0;
  padding: 0;
  width: 100%;
  max-width: 980px;
  align-items: center;
  justify-content: space-around;
  list-style: none;
}
.Header a {
  display: inline-block;
}
@media (max-width: 959px) {
  .Header li:not(.Header-logo) {
    display: none;
  }
}
.Logo {
  height: 140px;
}
.BgStretch {
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: 400px;
  position: relative;
}
@media (min-width: 768px) {
  .BgStretch {
    height: 800px;
  }
}
.Map {
  position: relative;
}
.Map > * {
  left: 0;
  transform: translateY(-50%);
}
.Top {
  background-image: url('../media/main.jpg');
}
.Top-content {
  text-align: center;
  position: absolute;
  max-width: 1000px;
  left: 0;
  right: 0;
  padding: 0 15px;
  margin: 0 auto;
  bottom: 30px;
}
@media (min-width: 768px) {
  .Top-content {
    bottom: 60px;
  }
}
.Hashtag {
  background-image: url('../media/hashtag.jpg');
}
.Section {
  padding: 30px 0;
  text-align: center;
}
@media (min-width: 768px) {
  .Section {
    padding: 60px 0;
  }
}
.Section--alt {
  background: #f3f1f0;
}
.RegistryList {
  list-style: none;
  padding: 45px;
  margin: -15px 0 0 -15px;
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
  text-align: center;
}
.RegistryList > * {
  padding-left: 15px;
  padding-top: 15px;
  width: 100%;
  flex: 0 0 100%;
}
@media (min-width: 768px) {
  .RegistryList > * {
    width: 33.333333%;
    flex-basis: 33.333333%;
  }
}
.RegistryList a {
  display: inline-block;
}
.BBB,
.CB {
  width: 200px;
  height: 100px;
}
.Target {
  width: 100px;
  height: 100px;
}
.SurveyContainer {
  position: relative;
}
.Countdown {
  background-image: url('../media/countdown.jpg');
}
.Countdown-content {
  text-align: center;
  position: absolute;
  max-width: 1000px;
  left: 0;
  right: 0;
  padding: 0 15px;
  margin: 0 auto;
  top: 60px;
}
@media (min-width: 768px) {
  .Countdown-content {
    top: 200px;
  }
}
.Countdown span {
  display: block;
  color: #fff;
  font-size: 50px;
  font-weight: bold;
  text-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
}
@media (min-width: 768px) {
  .Countdown span {
    font-size: 100px;
  }
}
.Countdown button {
  cursor: default;
  pointer-events: none;
}
@media (max-width: 767px) {
  .Countdown .uk-button {
    padding: 10px;
    line-height: 1;
  }
}
